/**
 * ‡πÅ‡∏õ‡∏•‡∏á‡∏£‡∏π‡∏õ‡πÅ‡∏ö‡∏ö‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà‡πÄ‡∏õ‡πá‡∏ô‡∏†‡∏≤‡∏©‡∏≤‡πÑ‡∏ó‡∏¢
 * @param {string} dateStr - ‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà‡πÉ‡∏ô‡∏£‡∏π‡∏õ‡πÅ‡∏ö‡∏ö "YYYY-MM-DD"
 * @returns {string} - "19 ‡∏û.‡∏¢. 2568"
 */
export function formatThaiDate(dateStr) {
  if (!dateStr) return 'N/A'
  const [year, month, day] = dateStr.split('-')
  const thaiMonths = ['‡∏°.‡∏Ñ.', '‡∏Å.‡∏û.', '‡∏°‡∏µ.‡∏Ñ.', '‡πÄ‡∏°.‡∏¢.', '‡∏û.‡∏Ñ.', '‡∏°‡∏¥.‡∏¢.', 
                      '‡∏Å.‡∏Ñ.', '‡∏™.‡∏Ñ.', '‡∏Å.‡∏¢.', '‡∏ï.‡∏Ñ.', '‡∏û.‡∏¢.', '‡∏ò.‡∏Ñ.']
  return `${parseInt(day)} ${thaiMonths[parseInt(month)-1]} ${parseInt(year)+543}`
}

/**
 * ‡πÅ‡∏õ‡∏•‡∏á‡πÄ‡∏ß‡∏•‡∏≤ 24hr ‡πÄ‡∏õ‡πá‡∏ô 12hr
 * @param {string} timeStr - ‡πÄ‡∏ß‡∏•‡∏≤‡πÉ‡∏ô‡∏£‡∏π‡∏õ‡πÅ‡∏ö‡∏ö "08:00:00" ‡∏´‡∏£‡∏∑‡∏≠ "08:00"
 * @returns {string} - "08:00 ‡∏ô."
 */
export function formatTime(timeStr) {
  if (!timeStr) return 'N/A'
  const time = timeStr.split(':')
  return `${time[0]}:${time[1]} ‡∏ô.`
}
